import React from 'react'
import { NavLink } from 'react-router-dom'
import './sidebaritem.scss'

function SidebarItem({ link, title, img, without, icon }) {
    return (
        <NavLink to={link} className='sidebar-item'>
            <div className="sidebar-item-wrapper">
                {
                    img ?
                        <img height='22.75' src={img} alt="" className="siedebar-item-wrapper__img" />
                        :
                        icon
                }
                <h4 className="sidebar-item-wrapper__title">
                    {title}
                </h4>
            </div>
        </NavLink>
    )
}

export default SidebarItem
