import React from 'react'
import './rightSide.scss'

import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import MoreHorizOutlinedIcon from '@mui/icons-material/MoreHorizOutlined';
import User1 from '../../assets/images/img/user1.png'

import User from '../user';
import { Link } from 'react-router-dom';

function RightSide() {
    return (
        <div className='right-side'>
            <div className="right-side-top">
                <input placeholder='Search Twitter' type="search" className="right-side__input" />
            </div>

            <div className="trends">
                <div className="trends-top">
                    <h3 className="trends-top__titl">Trends for you</h3>
                    <SettingsOutlinedIcon />
                </div>

                <div className="trends-item">
                    <div className="trends-top">
                        <h4 className="trends-item__title">Trending in Germany</h4>
                        <MoreHorizOutlinedIcon />
                    </div>
                    <h4 className="trends-item__subtitle">Revolution</h4>
                    <h4 className="trends-item__title">50.4K Tweets</h4>
                </div>

                <div className="trends-item">
                    <div className="trends-top">
                        <h4 className="trends-item__title">Trending in Germany</h4>
                        <MoreHorizOutlinedIcon />
                    </div>
                    <h4 className="trends-item__subtitle">Revolution</h4>
                    <h4 className="trends-item__title">50.4K Tweets</h4>
                </div>
            </div>

            <div className="trends">
                <h4 className="trends__title">You might like</h4>

                <div className="user-wrapper">
                    <User
                        img={User1}
                        name='Designsta'
                        email='@inner'
                    />

                    <Link className='trends-link' to='/'>Show more</Link>
                </div>
            </div>
        
            <h4 className='author'>It is Twitter clone by Nizamkhujaev Saidabror</h4>
        </div>
    )
}

export default RightSide
