import React from 'react'
import {auth, provider} from '../../Firebase/firebase'

import Logo from '../../assets/images/icon/logo.svg'

import './login.scss'

function Login() {

    function signIn() {
        auth.signInWithPopup(provider).then(res => {
            window.localStorage.setItem('userName', res.additionalUserInfo.profile.name)
            window.localStorage.setItem('userEmail', res.additionalUserInfo.profile.email)
            window.localStorage.setItem('userImg', res.additionalUserInfo.profile.picture)
            window.location.reload();
        }).catch(err => alert(err))
    }

    //name email picture

    return (
        <div className='login'>
            <div className="login-inner">
                <img src={Logo} alt="" className="login__img" />
                <h4 className="login__title">Log in to Twitter</h4>
                <button className='login__btn' onClick={signIn}>Sign in with Google</button>
            </div>
        </div>
    )
}

export default Login
