import React from 'react'
import './message.scss'

import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import MessageItem from '../messageItem';

import Comment from '../../assets/images/icon/comment.svg'
import Retweet from '../../assets/images/icon/retweet.svg'
import Like from '../../assets/images/icon/like.svg'
import Share from '../../assets/images/icon/share.svg'
import Statistics from '../../assets/images/icon/statistics.svg'

function Message({ img, name, text, email, time, post }) {

    return (
        <div className='message'>
            <div className="message-left">
                <img src={img} alt="" className="message-left__img" />
            </div>

            <div className="message-right">
                <div className="message-right-wrapper">
                    <h4 className="message-right__name">
                        {name} <span>{email}</span> <span>{time}</span>
                    </h4>
                    <MoreHorizIcon />
                </div>
                <p className="message-right__text">
                    {text}
                </p>

                {
                    post ? <img width='679' height='453' src={post} alt="post" className="message-right__img" /> : ''
                }

                <div className="message-right-bottom">
                    <MessageItem
                        img={Comment}
                        num={8}
                    />

                    <MessageItem
                        img={Retweet}
                        num={8}
                    />

                    <MessageItem
                        img={Like}
                        num={8}
                    />

                    <MessageItem
                        img={Share}
                    />

                    <MessageItem
                        img={Statistics}
                    />
                </div>

            </div>
        </div>
    )
}

export default Message
