import React, { useState, useEffect } from 'react'
import './home.scss'
import 'firebase/compat/firestore';
import { auth, db } from '../../Firebase/firebase'

import Star from '../../assets/images/icon/star.svg'
import User from '../../assets/images/img/user.png'
import User1 from '../../assets/images/img/user1.png'
import User3 from '../../assets/images/img/user3.png'
import Post from '../../assets/images/img/post.png'
import { Loader } from '../../components/loader/Loader';

import Image from '../../assets/images/icon/image.svg'
import Gif from '../../assets/images/icon/gif.svg'
import Stats from '../../assets/images/icon/stats.svg'
import Smile from '../../assets/images/icon/smile.svg'
import Schedule from '../../assets/images/icon/schedule.svg'

import Message from '../../components/message'

function Home() {

    const [state, setState] = useState('')
    const [tweets, setTweets] = useState([])

    function handleSubmit(e) {
        e.preventDefault();

        db.collection("tweets").add({
            name: window.localStorage.getItem('userName'),
            img: window.localStorage.getItem('userImg'),
            timestamp:  new Date().getTime(),
            text: state,
            email: window.localStorage.getItem('userEmail')
        })
        setState('')
    }


    useEffect(() => {
        db.collection("tweets").orderBy('timestamp', 'desc').onSnapshot(snapshot => (
            setTweets(
                snapshot.docs.map((doc) => ({
                    id: doc.id,
                    data: doc.data(),
                })))
        ))
    }, [])

    function signOut() {
        auth.signOut()
        window.localStorage.clear();
    }

    return (
        <div className='home'>
            {/* <button onClick={signOut} >Sign out</button>  */}

            <div className="home-top">
                <h4 className="home-top__title">
                    Home
                </h4>
                <img
                    style={{cursor: 'pointer'}}
                    src={Star}
                    alt=""
                    className="home-top__img"
                    onClick={signOut}
                />
            </div>

            <div className="tweet-blog">
                <div className="tweet-blog-left">
                    <img width='45' height='45' style={{ objectFit: 'cover', borderRadius: '50%' }} src={window.localStorage.getItem('userImg')} alt={window.localStorage.getItem('userName')} className="tweet-blog-left__img" />
                </div>

                <div className="tweet-blog-right">
                    <form className="tweet-blog-right-form">
                        <input
                            placeholder='What’s happening'
                            type="text"
                            className="tweet-blog-right-form__input"
                            value={state}
                            onChange={e => setState(e.target.value)}
                        />

                        <div className="tweet-blog-right-form-bottom">
                            <div className="tweet-blog-right-form-bottom-left">
                                <img width='24' height='24' src={Image} alt="" className="tweet-blog-right-form-bottom-left__img" />
                                <img width='24' height='24' src={Gif} alt="" className="tweet-blog-right-form-bottom-left__img" />
                                <img width='24' height='24' src={Stats} alt="" className="tweet-blog-right-form-bottom-left__img" />
                                <img width='24' height='24' src={Smile} alt="" className="tweet-blog-right-form-bottom-left__img" />
                                <img width='24' height='24' src={Schedule} alt="" className="tweet-blog-right-form-bottom-left__img" />
                            </div>

                            <div className="tweet-blog-right-form-bottom-right">
                                <button
                                    disabled={state.length > 0 ? false : true}
                                    className="tweet-blog-right-form-bottom-right__btn"
                                    onClick={handleSubmit}
                                >Tweet</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>

            <div className="message-block" style={{ width: '100%' }}>

                {
                    tweets.length > 0 ? (
                        tweets.map(({ id, data: { name, img, text, email, timestamp } }) => (
                            <Message
                                name={name}
                                key={id}
                                email={email}
                                img={img}
                                text={text}
                            />
                        ))
                    ) : <div className="loader-wrapper">
                        <Loader/>
                    </div>
                }

                <Message
                    name='Designsta'
                    time='25min'
                    email='@inner'
                    img={User}
                    text='Twitterdagi ayol-erkak qarama-qarshiliginglardan o`zinglar zerikmadinglarmi'
                />

                <Message
                    name='cloutexhibition'
                    time='22m'
                    email='@RajLahoti'
                    img={User1}
                    text='YPIP dasturining bu yilgi sezoni ham o’z nihoyasiga yetmoqda. Mentorlik davomida talaba va yangi bitiruvchilarni o’sayotganini ko’rib hursand bo’ladi odam.'
                />

                <Message
                    name='CreativePhoto'
                    time='1h'
                    email='@cloutexhibition'
                    img={User3}
                    text='Обетда..... 
                    Кечиринглар'
                    post={Post}
                />
            </div>

        </div>
    )
}

export default Home
