import React from 'react'
import './user.scss'

function User({img, name, email}) {
    return (
        <div className='user'>
            <div className="user-left">
                <img src={img} alt="name" className="user-left__img" />
                <div className="user-left-wrapper">
                    <h4 className="user-left-wrapper__name">{name}</h4>
                    <h5 className="user-left-wrapper__email">{email}</h5>
                </div>
            </div>
            <button className="user-right">Follow</button>
        </div>
    )
}

export default User
