import React from 'react'
import './sidebar.scss'

import Logo from '../../assets/images/icon/logo.svg'
import saved from '../../assets/images/icon/save.png'
import hashTag from '../../assets/images/icon/hashtag.svg'
import NotificationsIcon from '../../assets/images/icon/notification.svg'
import More from '../../assets/images/icon/more.svg'

import SidebarItem from '../sidebarItem'

import HomeIcon from '@mui/icons-material/Home';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import ListAltIcon from '@mui/icons-material/ListAlt';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';


function Sidebar() {
    return (
        <div className='sidebar'>
            <div className="sidebar-top">
                <img src={Logo} alt="Logo" width='40' className="sidebar-top__logo" />

                <div className="sidebar-top-wrapper">
                    <SidebarItem
                        title='Home'
                        link='/'
                        icon={<HomeIcon />}
                    />

                    <SidebarItem
                        title='Explore'
                        link='/explore'
                        img={hashTag}
                    />

                    <SidebarItem
                        title='Notifications'
                        link='/notifications'
                        img={NotificationsIcon}
                    />

                    <SidebarItem
                        title='Messages'
                        link='/messages'
                        icon={<MailOutlineIcon />}
                    />

                    <SidebarItem
                        title='Bookmarks'
                        link='/bookmarks'
                        img={saved}
                    />

                    <SidebarItem
                        title='Lists'
                        link='/lists'
                        icon={<ListAltIcon/>}
                    />

                    <SidebarItem
                        title='Profile'
                        link='/profile'
                        icon={<PersonOutlineIcon/>}
                    />

                    <SidebarItem
                        title='More'
                        link='/more'
                        img={More}
                    />

                    <button className="tweet">Tweet</button>

                </div>
            </div>

            <div className="sidebar-bottom">
                <div className="sidebar-bottom-left">
                    <img width='30' height='30' style={{objectFit: 'cover', borderRadius: '50%'}} src={window.localStorage.getItem('userImg')} alt={window.localStorage.getItem('userName')} className="sidebar-bottom-left__img" />
                </div>

                <div className="sidebar-bottom-right">
                    <h4 className="sidebar-bottom-right__title">{window.localStorage.getItem('userName')}</h4>
                    <h4 className="sidebar-bottom-right__subtitle">@{window.localStorage.getItem('userEmail')}</h4>
                </div>
            </div>
        </div>
    )
}

export default Sidebar
